<template>
  <div style="display: flex">
    <div class="intro-text" style="min-height:calc(100vh - 60px);width: 155px;background-color: #ecedf1;flex-shrink: 0;">
      <el-menu :default-openeds="['info']" :collapse="isCollapse" :collapse-transition="false" router background-color="#ecedf1" text-color="#222221"
               active-text-color="#fff" style="border: none" :default-active="$route.path">
        <el-menu-item index="/user" >
          <i class="el-icon-caret-right"></i>
          <span slot="title">用户管理</span>
        </el-menu-item>
        <el-menu-item index="/notice" >
          <i class="el-icon-caret-right"></i>
          <span slot="title">通知公告</span>
        </el-menu-item>
      </el-menu>
      <div style="padding-top: 160px">
        <img src="../../assets/金色时代.png" height="234" width="153"/>
      </div>
    </div>

    <div style="padding: 20px 0 0 20px;width: 100%">
      <div>
        <el-input style="width: 300px;margin-right: 10px" placeholder="请输入搜索内容" v-model="searchText"></el-input>
        <el-button type="primary" @click="load(1)">查询</el-button>
        <el-button type="info" @click="reset">重置</el-button>
      </div>
      <div style="margin: 10px 0">
        <el-button type="primary" plain @click="handleAdd">新增</el-button>
        <el-button type="danger" plain @click="delBatch" v-if="user.authority==='超级管理员'">批量删除</el-button>
      </div>
      <el-table :data="tableData" stripe :header-cell-style="{ backgroundColor: 'aliceblue', color: '#666' }" @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column prop="id" label="序号" width="70" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column prop="title" label="标题" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column prop="content" label="内容" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column prop="userName" label="发布人" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column prop="date" label="发布时间" :show-overflow-tooltip="true"></el-table-column>
<!--        <el-table-column label="是否公开">-->
<!--          <template v-slot="scope">-->
<!--            <el-switch v-model="scope.row.open" @change="changeOpen(scope.row)"></el-switch>-->
<!--          </template>-->
<!--        </el-table-column>-->
        <el-table-column prop="type" label="通知类型" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column prop="pusher" label="推送人员" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column prop="department" label="部门" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="操作" align="center" width="180" fixed="right">
          <template v-slot="scope">
            <el-button size="mini" type="primary" plain @click="handleEdit(scope.row)" v-if="user.authority==='管理员' || user.authority==='超级管理员'">编辑</el-button>
            <el-button size="mini" type="danger" plain @click="del(scope.row.id)" v-if="user.authority==='超级管理员'">删除</el-button>
          </template>
        </el-table-column>
      </el-table>

      <div style="margin: 10px 0">
        <el-pagination
            @current-change="handleCurrentChange"
            :current-page="pageNum"
            :page-size="pageSize"
            layout="total, prev, pager, next"
            :total="total">
        </el-pagination>
      </div>

      <el-dialog title="公告信息" :visible.sync="fromVisible" width="40%" :close-on-click-modal="false">
        <el-form :model="form" label-width="80px" style="padding-right: 20px" :rules="rules" ref="formRef">
          <el-form-item label="标题" prop="title">
            <el-input v-model="form.title" placeholder="标题"></el-input>
          </el-form-item>
          <el-form-item label="通知类型" prop="type">
            <el-input  v-model="form.typeList" placeholder="通知类型"></el-input>
          </el-form-item>
          <el-form-item label="推送人员" prop="pusher">
            <el-select v-model="form.pusher" >
              <el-option
                  v-for="item in pusherList"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="内容" prop="content">
            <el-input type="textarea" v-model="form.content" placeholder="内容"></el-input>
          </el-form-item>
          <el-form-item label="部门" prop="department">
            <el-select v-model="form.department" >
              <el-option
                  v-for="item in departmentList"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>

        <div slot="footer" class="dialog-footer">
          <el-button @click="fromVisible = false">取 消</el-button>
          <el-button type="primary" @click="save">确 定</el-button>
        </div>
      </el-dialog>

    </div>

  </div>

</template>

<script>

import axios from "axios";

export default {
  name: "Notice",
  data() {
    return {
      searchText: '',
      tableData: [],
      pageNum: 1,   // 当前的页码
      pageSize: 10,  // 每页显示的个数
      title: '',
      type: '',
       notices: [],
      userSpecificData:[],
      allMemberData:[],
      department:'',
      total: 0,
      total01: 0,
      total02: 0,
      fromVisible: false,
      form: {},
      user: JSON.parse(localStorage.getItem('honey-user') || '{}'),
      rules: {
        title: [
          { required: true, message: '请输入标题', trigger: 'blur' },
        ],
        content: [
          { required: true, message: '请输入内容', trigger: 'blur' },
        ]
      },
      ids: [],
      content: '',
      departmentList: [
        {value: '设备部', label: '设备部'},
        {value: '动力车间', label: '动力车间'},
        {value: '技术部', label: '技术部'}
      ],
      pusherList: [
        {value: '全体人员', label: '全体人员'},
        {value: '点检员', label: '点检员'},
        {value: '维修工', label: '维修工'},
        {value: '安全员', label: '安全员'}
      ],
      typeList: '',

    }
  },
  //created() {

   // this.fetchData();
  //},
  mounted:function(){
   // this.member()
    this.load()
  //  this.fetchData();// 封装数据加载逻辑到一个函数中

 },
  methods: {
    changeOpen(form) {
      // 调用更新的接口  更新数据到数据库
      this.form = JSON.parse(JSON.stringify(form))
      this.sendSaveRequest()   // 直接发请求就可以了
    },
    delBatch() {
      if (!this.ids.length) {
        this.$message.warning('请选择数据')
        return
      }
      this.$confirm('您确认批量删除这些数据吗？', '确认删除', {type: "warning"}).then(response => {
        this.$request.delete('/notice/delete/batch', { data: this.ids }).then(res => {
          if (res.code === '200') {   // 表示操作成功
            this.$message.success('操作成功')
            this.load(1)
          } else {
            this.$message.error(res.msg)  // 弹出错误的信息
          }
        })
      }).catch(() => {})
    },
    handleSelectionChange(rows) {   // 当前选中的所有的行数据
      this.ids = rows.map(v => v.id)
    },
    del(id) {
      this.$confirm('您确认删除吗？', '确认删除', {type: "warning"}).then(response => {
        this.$request.delete('/notice/delete/' + id).then(res => {
          if (res.code === '200') {   // 表示操作成功
            this.$message.success('操作成功')
            this.load(1)
          } else {
            this.$message.error(res.msg)  // 弹出错误的信息
          }
        })
      }).catch(() => {})
    },
    handleEdit(row) {   // 编辑数据
      this.form = JSON.parse(JSON.stringify(row))  // 给form对象赋值  注意要深拷贝数据
      this.fromVisible = true   // 打开弹窗
    },
    handleAdd() {   // 新增数据
      this.form = {}  // 新增数据的时候清空数据
      this.fromVisible = true   // 打开弹窗
    },
    save() {   // 保存按钮触发的逻辑  它会触发新增或者更新
      this.$refs.formRef.validate((valid) => {
        if (valid) {
          this.sendSaveRequest()
        }
      })
    },
    sendSaveRequest() {
      this.$request({
        url: this.form.id ? '/notice/update': '/notice/add',
        method: this.form.id ? 'PUT' : 'POST',
        data: this.form
      }).then(res => {
        if (res.code === '200') {  // 表示成功保存
          this.$message.success('保存成功')
          this.load(1)
          this.fromVisible = false
        } else {
          this.$message.error(res.msg)  // 弹出错误的信息
        }
      })
    },
    reset() {
      this.searchText = '';
      this.load(1);
    },
    load(pageNum) {
      if (pageNum) this.pageNum = pageNum;

      // 根据用户权限设置department
      if (this.user.authority === '超级管理员','管理员'){
        this.department = '';
        this.pusher = '';
        const userSpecificDataPromise = this.$request.get('/notice/selectByPage', {
          params: {
            pageNum: this.pageNum,
            pageSize: this.pageSize,
            searchText: this.searchText // 直接传递 searchText 参数
          }
        }).then(res => {
          this.total = res.data.total; // 设置总条数
          // 假设res.data.records是当前用户权限下的数据
          return res.data.records;

        });
        // 不发送全体人员数据的请求
        userSpecificDataPromise.then(userSpecificData => {
          this.userSpecificData = userSpecificData;
          this.tableData = userSpecificData; // 直接赋值给tableData，因为没有全体人员数据
        }).catch(error => {
          console.error('加载用户特定数据出错：', error);
        });
      } else {
        this.department = this.user.department;
        this.pusher = this.user.authority;
        // 首先定义两个用于请求的promises
        const userSpecificDataPromise = this.$request.get('/notice/selectByPage', {
          params: {
            pageNum: this.pageNum,
            pageSize: this.pageSize,
            title: this.title,
            type: this.type,
            pusher: this.pusher,  // 使用当前用户的权限
            department: this.department
          }
        }).then(res => {

          // 假设res.data.records是当前用户权限下的数据
          return res.data.records;

        });

        const allMemberDataPromise = this.$request.get('/notice/selectByPage', {
          params: {
            pageNum: this.pageNum,
            pageSize: this.pageSize,
            title: this.title,
            type: this.type,
            pusher: '全体人员', // 专门获取"全体人员"权限下的数据
            department: '' // 如果"全体人员"不依赖于部门，这里可以是空字符串或其他默认值
          }
        }).then(res => {
          this.total = res.data.total; // 设置总条数
          // 假设res.data.records是"全体人员"权限下的数据
          return res.data.records;
        });

        // 使用Promise.all等待两个请求都完成
        Promise.all([userSpecificDataPromise, allMemberDataPromise]).then(([userSpecificData, allMemberData]) => {
          // 在这里，两个请求都已经完成，并且返回了数据
          this.userSpecificData = userSpecificData;
          this.allMemberData = allMemberData;

          // 合并两个数据集
          this.tableData = this.userSpecificData.concat(this.allMemberData);
          this.total = this.tableData.length;
          // 现在tableData包含了完整的数据，可以在页面上使用
          // 更新页面显示的数据条数
          this.updateDisplayedDataCount();
        }).catch(error => {
          // 处理错误情况
          console.error('加载数据出错：', error);
        });
      }
    },
    updateDisplayedDataCount() {
      // 更新页面显示的数据条数
      // 假设你有一个显示数据条数的 DOM 元素，它的 ID 是 dataCount
      document.getElementById('dataCount').textContent = this.total;
    },

    handleCurrentChange(pageNum) {
      this.load(pageNum)
    },


  }
}
</script>

<style>
.el-tooltip__popper{ max-width:300px !important; }
</style>